import { mapState } from "vuex";
import { updateUser } from "@/api/user";
export default {
  middleware: "authenticated",
  name: "SettingIndex",
  data() {
    return {};
  },
  computed: {
    ...mapState(["user"]),
  },
  methods: {
    async submit() {
      const { data } = await updateUser({
        email: this.user.email,
        bio: this.user.bio,
        image: this.user.image,
      });
      console.log(data);
    },
  },
};
